.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.parallax {
  /* The image used */
  background-image: url("https://www.sincable.mx/wp-content/uploads/2020/03/2-PC-gamer-constant-loubier-7lzIyp2Ork4-unsplash-scaled.jpg");

  /* Set a specific height   */
  height: 500px;

  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.parallax2 {
  /* The image used */
  background-image: url("https://cdn.computerhoy.com/sites/navi.axelspringer.es/public/styles/1200/public/media/image/2017/12/pc-gaming-willyrex-esports_0.jpg?itok=qfVkUQU1");

  /* Set a specific height */
  height: 500px;

  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.carta{
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 0;
  word-wrap: break-word;
  background-color: #fcf3f3;
  background-clip: border-box;
  border: 1px solid rgba(0,0,0,.125);
  border-radius: .25rem;
}

.login{
  margin: 0;
  position: absolute;
  top: 25%;

}

.footer{
  padding: 80px 60px;
  background: black;
  bottom: 0;
  width: 100%;
}

.footer-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 600px;
    margin: 0 auto;
}

.footer-column{
  display: flex;
  flex-direction: column;
  text-align: left;
  margin-left: 60px;
}

.footer-row{
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(185px, 1fr));
  grid-gap: 20px;   
  @media (max-width: 1000px) {
    grid-template-columns: repeat(auto-fill, 
                           minmax(200px, 1fr));
  }
}

.footer-link{
  color: #fff;
  margin-bottom: 20px;
  font-size: 18px;
  text-decoration: none;   
}

.footer-heading{
  font-size: 24px;
  color: #fff;
  margin-bottom: 40px;
  font-weight: bold;
}

.inicio{
  margin: 40px;
  border: 2px solid #c40909;
  border-radius: 16px;
  font-size: 20px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
